import {
  Link,
  Router,
  Routes,
  Route,
  BrowserRouter,
  useParams,
  useLocation,
} from "react-router-dom";
import { About } from "./About";
import { Progress } from "./Progress";
import { GameDiary } from "./GameDiary";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import {
  Container,
  Navbar,
  Nav,
  Row,
  NavbarCollapse,
  NavbarToggle,
} from "react-bootstrap";
import { useState } from "react";
import { RenderPage } from "./RenderPage";
import { Nikki } from "./Nikki";

function App() {
  return (
    <BrowserRouter>
      <Container
        fluid
        className="overflow-hidden"
        style={{
          height: "100vh",
          backgroundImage: `url('${process.env.PUBLIC_URL}/photo1.jpg')`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
        }}
      >
        {/* <img src={`${process.env.PUBLIC_URL}/photo1.jpg`} /> */}
        <Header />
        <div
          className="d-flex flex-row align-item-start text-left"
          style={{ fontFamily: "Noto Sans JP" }}
        >
          <Tabs />
          <Router basename={process.env.PUBLIC_URL}>
            <Routes>
              <Route path="/About" element={<About />} />
              <Route path="/Progress" element={<Progress />} />
              <Route
                path="/Progress/:article"
                element={<RenderPage link={"/Progress"} />}
              />
              <Route path="/GameDiary" element={<GameDiary />} />
              <Route
                path="/GameDiary/:article"
                element={<RenderPage link={"/GameDiary"} />}
              />
              {/* <Route path="/PixelArt" element={<PixelArt />} /> */}
              <Route path="/Nikki" element={<Nikki />} />
              <Route
                path="/Nikki/:article"
                element={<RenderPage link={"/Nikki"} />}
              />
            </Routes>
          </Router>
        </div>
      </Container>
    </BrowserRouter>
  );
}

const Header = () => {
  return (
    <Row className="p-3" style={{ color: "white", fontFamily: "" }}>
      <Link to="/" style={{ textDecoration: "none", width: "60vw" }}>
        <h1 className="" style={{ fontSize: "36px", color: "white" }}>
          
        </h1>
      </Link>
      <p style={{ fontSize: "14px" }}></p>
      <span
        className="border"
        style={{ width: "60vw", marginTop: "10px" }}
      ></span>
    </Row>
  );
};

const Tabs = () => {
  const [isFinish1, setIsFinish1] = useState(false);
  const AnimationFinish1 = () => {
    setIsFinish1(true);
  };
  const [isFinish2, setIsFinish2] = useState(false);
  const AnimationFinish2 = () => {
    setIsFinish2(true);
  };
  const [isFinish3, setIsFinish3] = useState(false);
  const AnimationFinish3 = () => {
    setIsFinish3(true);
  };
  const [isFinish4, setIsFinish4] = useState(false);
  const AnimationFinish4 = () => {
    setIsFinish4(true);
  };
  const [isFinish5, setIsFinish5] = useState(false);
  const AnimationFinish5 = () => {
    setIsFinish5(true);
  };

  return (
    <Navbar
      className="navbar-expand-lg d-flex flex-column align-items-start "
      style={{
        width: "30vw",
        height: "80vh",
        marginLeft: "20px",
      }}
    >
      <Link
        to="/About"
        href="/page1"
        className={`tab ${isFinish1 ? "corsorhover" : "tab-slide1"}`}
        style={{ fontSize: "24px", marginTop: "20px" }}
        onAnimationEnd={AnimationFinish1}
      >
        About
      </Link>
      <Link
        to="/Progress"
        href="/page2"
        className={`tab ${isFinish2 ? "corsorhover" : "tab-slide2"}`}
        style={{ fontSize: "24px", marginTop: "20px" }}
        onAnimationEnd={AnimationFinish2}
      >
        
      </Link>
      <Link
        to="/GameDiary"
        href="/page3"
        className={`tab ${isFinish3 ? "corsorhover" : "tab-slide3"}`}
        style={{ fontSize: "24px", marginTop: "20px" }}
        onAnimationEnd={AnimationFinish3}
      >
        
      </Link>
      {/* <Link to="/PixelArt" href="/page4" className={`tab ${isFinish4 ? "corsorhover" : "tab-slide4"}`} style={{ fontSize: "24px", marginTop: "20px", marginLeft: "20px" }} onAnimationEnd={AnimationFinish4}></Link> */}
      <Link
        to="/Nikki"
        href="/page5"
        className={`mt-auto tab ${isFinish5 ? "corsorhover" : "tab-slide5"}`}
        style={{ fontSize: "18px", marginTop: "20px" }}
        onAnimationEnd={AnimationFinish5}
      >
        
      </Link>
    </Navbar>
  );
};

export default App;
